@keyframes chatbox-moving {
  0%   { right :30px; top:100%;;}
  
  100% { right :30px;; top:19%;;}
}

@media (max-width: 800px) {
  .chatbox{

  width: 90%;
  height: 87%;
  position: fixed;
 overflow-y: auto;
 top: 12%;
  left: 5%;
  background-image: url(https://i.pinimg.com/originals/58/c3/33/58c33377dfcbb3022493dec49d098b02.jpg);
   -webkit-animation-name: chatbox-moving;
           animation-name: chatbox-moving;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  overflow-y: auto;
  overflow-x: hidden;
  border-top-left-radius: 2%;
  border-top-right-radius : 2%;
}

@-webkit-keyframes chatbox-moving {
  0%   { left: 5%; top:100%;;}
  
  100% { left: 5%; top: 12%;}
}

@keyframes chatbox-moving {
  0%   { left: 5%; top:100%;;}
  
  100% { left: 5%; top: 12%;}
}
 } 

 .close {
  position: absolute;
  right: 10px;
  top: 5px;
  width: 25px;
  height: 30px;
  opacity: 0.7;
}
.close:hover {
  opacity: 1;
}

.close:before, .close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #fff;
}
.close:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

